import { Box, Container, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import styles from './style.module.scss';
import ankr from '../../assets/icons/ankr.svg';
import polygon from '../../assets/icons/polygon.svg';
import clv from '../../assets/icons/clv.svg';
import harmony from '../../assets/icons/harmony.svg';
import cere from '../../assets/icons/cere.svg';
import arpa from '../../assets/icons/arpa.svg';
import marlin from '../../assets/icons/marlin.svg';
import onbuff from '../../assets/icons/onbuff.svg';
import auction from '../../assets/icons/auction.svg';
import hats from '../../assets/icons/hats.svg';
import arbitrum from '../../assets/icons/arbitrum.png';
import scroll from '../../assets/icons/scroll.ico';
import milk from '../../assets/icons/milk.jpg';
import { useTranslation, withTranslation } from 'react-i18next';
import { apiVar } from '../../consts';
import axios from 'axios';

const works = [
  {
    src: scroll,
    width: '20%',
    title: 'Scroll'
  },
  {
    src: arbitrum,
    width: '20%',
    title: 'Arbitrum'
  },
  {
    src: ankr,
    width: '20%',
    title: 'Ankr'
  },
  {
    src: polygon,
    width: '20%',
    title: 'Polygon'
  },
  {
    src: auction,
    width: '20%',
    title: 'Auction'
  },
  {
    src: clv,
    width: '25%',
    title: 'CLV'
  },
  {
    src: milk,
    width: '25%',
    title: 'Milk'
  },
  {
    src: harmony,
    width: '25%',
    title: 'Harmony'
  },

  {
    src: cere,
    width: '25%',
    title: 'Cere'
  },
  {
    src: marlin,
    width: '25%',
    title: 'Marlin'
  },
  {
    src: onbuff,
    width: '25%',
    title: 'ONBUFF'
  },
  {
    src: arpa,
    width: '25%',
    title: 'ARPA'
  },
  {
    src: hats,
    width: '25%',
    title: 'Hats'
  }
];

const normalize = (payload, index) => {
  const width = index < 5 ? '20%' : '25%';

  return {
    src: apiVar + payload?.logo?.data?.attributes?.url,
    width,
    title: payload.name
  };
};

const Works = () => {
  const { t } = useTranslation('translation');
  const [works, setWorks] = useState([]);

  useEffect(() => {
    axios
      .get(`${apiVar}/api/works?populate=*`)
      .then((res) => {
        const { data } = res;

        setWorks(data?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Box className={styles.container}>
      <Container>
        <Typography className={styles.title}>
          {t('header.portfolio')}
        </Typography>
        <Box className={styles.works}>
          {works.map(({ attributes: work }, w) => (
            <Box
              key={w}
              width={normalize(work).width}
              display="flex"
              alignItems="center"
              className={styles.work}
            >
              <img
                src={normalize(work).src}
                alt="work item"
                width="40px"
                height="40px"
              />
              <Typography fontSize={26} fontWeight={600} ml={1}>
                {normalize(work).title}
              </Typography>
            </Box>
          ))}
        </Box>
      </Container>
    </Box>
  );
};

export default withTranslation()(Works);
