import { Box, Container, Typography, useMediaQuery } from '@mui/material';
import { useTheme } from '@mui/material/styles';
import React, { useEffect, useState } from 'react';
import Member from './Member';
import styles from './style.module.scss';
import classNames from 'classnames';
import { gsap } from 'gsap/dist/gsap';
import { useTranslation, withTranslation } from 'react-i18next';
import axios from 'axios';
import { apiVar } from '../../consts';

const Teams = () => {
  const theme = useTheme();
  const isMatches = useMediaQuery(theme.breakpoints.down('sm'));
  const { t } = useTranslation('translation');

  const [members, setMembers] = useState([]);

  useEffect(() => {
    if (isMatches || !isMatches) return;

    const ctx = gsap.context(() => {
      gsap.from('.member1', {
        scrollTrigger: {
          trigger: '.member1',
          toggleActions: 'play reverse play reverse',
          start: `1000s`,
          end: `2000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member2', {
        scrollTrigger: {
          trigger: '.member2',
          toggleActions: 'play reverse play reverse',
          start: `2500s`,
          end: `4000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member3', {
        scrollTrigger: {
          trigger: '.member3',
          toggleActions: 'play reverse play reverse',
          start: `3500s`,
          end: `6000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member4', {
        scrollTrigger: {
          trigger: '.member4',
          toggleActions: 'play reverse play reverse',
          start: `4500s`,
          end: `8000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.from('.member5', {
        scrollTrigger: {
          trigger: '.member5',
          toggleActions: 'play reverse play reverse',
          start: `5500s`,
          end: `8000000000s`
        },
        y: -80,
        opacity: 0
      });

      gsap.to('.teams', {
        scrollTrigger: {
          pin: '.teams',
          end: `${
            Array.from([...members.map((_, index) => index + 1)]).length *
              1000 *
              1.8 -
            1000
          }s`,
          pinSpacing: true
        }
      });
    });

    return () => ctx.revert();
  }, [isMatches]);

  useEffect(() => {
    axios
      .get(`${apiVar}/api/teams?populate=*`)
      .then((res) => {
        const { data } = res;

        setMembers(data?.data);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Box className={classNames(styles.container, 'teams')}>
      <Container className={styles.wrapper}>
        <Typography className={classNames(styles.title, 'team-title')}>
          {t('our_team')}
        </Typography>
        <Box display="flex" className={styles.members}>
          {members.map(({ attributes: member }, index) => {
            return (
              <Box
                key={index}
                className={classNames(
                  styles['member' + (index + 1)],
                  `member${index + 1}`,
                  styles.member
                )}
              >
                <Member
                  name={member?.name}
                  imgSrc={member?.main_img?.data?.attributes?.url}
                  hoverSrc={member?.hover_img?.data?.attributes?.url}
                  staff={member?.staff}
                  tooltipPosition={member?.tooltip_position}
                  tooltip={member?.tooltip}
                />
              </Box>
            );
          })}
        </Box>
      </Container>
    </Box>
  );
};

export default withTranslation()(Teams);
